<template>
  <div class="wrap">
    <div class="header">
    <ul class="header-button-left">
      <li @click="step = 1">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step== 2" @click="step++">Next</li>
      <li v-if="step ==3" @click="publish()">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <!-- <h1>{{ name }}</h1>
  <button @click="$store.commit('이름변경')">버튼</button>
  <p>{{ $store.state.more }}</p>
  <button @click="$store.dispatch('getData')">더보기</button>
  <p>{{ now2 }} {{ 카운터 }}</p>
  <p>{{ age }}</p>
  <p>{{ likes }}</p>
  <button @click="카운터++">버튼</button> -->

  <container @add-posts="addPosts" @increment-more="incrementMore" :포스트데이터="포스트데이터" @update-like="handleUpdateLike" :step="step" :이미지="이미지" :선택필터="선택필터" @upload="upload" @write="작성글 = $event">
  </container> 

  <div class="btn-box">
    <button class="menu-btn" @click="step = 1"><i class="bi bi-house-door-fill"></i></button>
    <button class="menu-btn" @click="step = 2"><i class="bi bi-funnel-fill"></i></button>
    <button class="menu-btn" @click="step = 3"><i class="bi bi-cloud-arrow-up-fill"></i></button>
  </div>

  <div class="footer" v-if="step === 1">
    <ul class="footer-button-plus">
      <input type="file" @change="upload"  id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
 </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex';
import Container from './components/containerBox.vue'
import postData from './db/data'

export default {
  name: 'MyApp',
  components: {
    Container,
  },
  data(){
    return{
      step:1,
      이미지:'',
      포스트데이터:postData,
      작성글:'',
      선택필터:'',
      카운터:0,
    }
  },
  mounted(){
    this.emitter.on('박스클릭함', (a)=>{
      this.선택필터 = a
    })
  },
  computed:{
    now2(){
      return new Date()
    },
    ...mapState([
    'name','age','liked'
    ])
  },
  methods:{
    ...mapMutations(['setMore','likes','liked']),
    addPosts(posts){
      this.포스트데이터.push(...posts)
    },
    incrementMore(newValue) {
      this.더보기 = newValue; // 더보기 값을 부모에서 처리
    },
    now(){
      return new Date()
    },
    upload(e){
    let 파일 = e.target.files;
    let url = URL.createObjectURL(파일[0]);
    console.log(url);
    this.이미지 = url;
    this.step++
  },
  handleUpdateLike({ liked, likesChange }) {
      this.포스트데이터.liked = liked; // liked 상태 업데이트
      this.포스트데이터.likes += likesChange; // like 수 업데이트
    },
  publish(){
    var 내게시물 = {
        name: "Kim Hyu1n",
        userImage: "https://picsum.photos/100?random=1",
        postImage: this.이미지,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.작성글,
        filter: this.선택필터
      };
      this.포스트데이터.unshift(내게시물);
      this.step = 1;
  },
  }
}
</script>

