import { createStore } from 'vuex'
import axios from 'axios'

const store = createStore({
  state(){
    return {
     name:'kimp',
     liked:false,
     likes:[0,0,0],
     more:{},
     age:18
    }
  },
  mutations:{
    이름변경(state){
      state.name = 'park'
      // state는 위의 데이터를 의미한다. this 같은 거라고 보면 될 듯
    },
    좋아요추가(state, a){
      if(state.liked == false){
        state.likes[a]++;
        state.liked = true;
      }else{
        state.likes[a]--;
        state.liked = false;
      }
      // state.liked = !state.liked
      // state.likes += state.liked ? 1:-1;
    },
    setMore(state, data){
      state.more = data
    }
  },
  actions:{
    getData(context){
      axios.get('https://raw.githubusercontent.com/kcy230426/new.github.io/refs/heads/main/books1.js').then((a)=>{
       context.commit('setMore', a.data)
      })
    }
  },
})

export default store