
<template>
    <div>
        <posting :포스트데이터="a" v-for="(a,i)  in 포스트데이터" :key="i" :step="step" @update-like="forwardUpdateLike"/>
        <button v-if="step === 1" @click="moreClick" class="more-btn">more<i class="bi bi-plus"></i></button>

        <div class="step-two" v-if="step === 2">
            <div :class="['upload-image', 선택필터]" :style="{backgroundImage:'url('+이미지+')'}"></div>
            <div class="filters">
                <filterBox :이미지="이미지" :필터="필터"  v-for="필터 in 필터들" :key="필터">
                    {{ 필터 }}
                    <!-- 처음에 반복문 돌릴 변수명과 :필터="값" 안에 들어가는 값명이 같아서 각각의 항목에 맞는 이름이 아닌 전체가 출력되는 이슈가 있었다. -->
                </filterBox>
            </div>
        </div>

        <!-- 글작성페이지 -->
        <div class="step-three" v-if="step === 3">
            <div :class="['upload-image', 선택필터]" :style="{backgroundImage:'url('+이미지+')'}">
            </div>
            <div class="write">
                <textarea class="write-box" @input="$emit('write', $event.target.value)" v-model="글">
                </textarea>
            </div>
        </div>

    </div>
</template>
<script>
    import Posting from './vuestaPost.vue'
    import axios from 'axios'
    import filterBox from './filterBox.vue';

    export default{
        name:'containerBox',
        components:{Posting,filterBox},
        data(){
            return{
                더보기:0,
                필터들:[ "aden", "_1977", "brannan", "brooklyn", "clarendon", "earlybird", "gingham", "hudson", 
                        "inkwell", "kelvin", "lark", "lofi", "maven", "mayfair", "moon", "nashville", "perpetua", 
                       "reyes", "rise", "slumber", "stinson", "toaster", "valencia", "walden", "willow", "xpro2"]
            }
        },
        props:{
            step:Number,
            이미지:String,
            선택필터:String,
            포스트데이터:Array
        },
        methods:{
            moreClick(){
                axios.get(`https://codingapple1.github.io/vue/more${this.더보기}.json`)
                .then(결과=>{
                    this.$emit('add-posts', 결과.data);
                    this.$emit('increment-more', this.더보기++);
                }).catch(()=>{
                    console.log("에러 남")
                })
            },
            forwardUpdateLike(event) {
      // 전달받은 이벤트를 다시 상위로 emit
      this.$emit("update-like", event);
    },
        }
    }
</script>
<style>
.more-btn{
    border: none;
    padding: 15px 40px;
    border-radius: 50px;
    display: block;
    margin: 0 auto;
    margin-top: 20px;
    cursor: pointer;
    transition: 0.3s;
    margin-bottom: 20px;
}
.more-btn:hover{
    background-color: black;
    color: white;
}
.upload-image{
width: 100%;
height: 450px;
background: #000000;
background-size: contain;
background-repeat: no-repeat;
background-position: center;
}
.filters{
    background-color: black;
overflow-x:scroll;
white-space: nowrap;
}
.filter-1 {
width: 100px;
height: 100px;
background-color: cornflowerblue;
margin: 10px 10px 10px auto;
padding: 8px;
display: inline-block;
color : white;
background-size: cover;
}
.filters::-webkit-scrollbar {
height: 5px;
}
.filters::-webkit-scrollbar-track {
background: #f1f1f1; 
}
.filters::-webkit-scrollbar-thumb {
background: #888; 
border-radius: 5px;
}
.filters::-webkit-scrollbar-thumb:hover {
background: #555; 
}
.write-box {
border: none;
width: 90%;
height: 100px;
padding: 15px;
margin: auto;
display: block;
outline: none;
}
</style>